.hero {
    width: 100%;
    background-color: #7C4EE4;
    padding: 180px 0 100px 0;
    overflow: hidden;
}

.hero__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    position: relative;
}

.hero__container::before {
    content: '';
    width: 100%;
    max-width: 685px;
    height: 378px;
    display: block;
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    left: -215px;
    top: -300px;
    background-image: url(/public/decor/hero-decor.png);
}

.hero__container::after {
    content: '';
    width: 100%;
    max-width: 685px;
    height: 378px;
    display: block;
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    right: -215px;
    bottom: -300px;
    background-image: url(/public/decor/hero-decor-2.png);
}

.hero__image {
    border-radius: 20px;
    width: 100%;
    height: 100%;
    max-width: 608px;
    max-height: 576px;
}

.hero__text {
    display: flex;
    flex-direction: column;
    max-width: 570px;
}

.hero__text span {
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 2px;
    margin-bottom: 35px;
}

.hero__text h1 {
    color: #fff;
    font-size: clamp(36px, 5vw, 64px);
    font-weight: 700;
    margin-bottom: 25px;
}

.hero__text p {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
}

.hero__button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 35px;
    max-width: 170px;
    background-color: #fff;
    transition-duration: .5s;
    color: #373737;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    border: 2px solid transparent;
    border-radius: 8px;
    text-decoration: none;
    margin-top: 60px;
}

.hero__button:hover {
    border: 2px solid #fff;
    background-color: #7C4EE4;
    color: #fff;
}

@media (max-width: 1200px) {
    .hero__image {
        max-width: 408px;
        max-height: 376px;
    }
}

@media (max-width: 992px) {
    .hero {
        padding: 120px 0 40px 0;
    }

    .hero__container {
        flex-direction: column;
    }

    .hero__container::before {
        left: 0;
    }

    .hero__container::after {
        right: 0;
    }
}